import React, { useEffect, useState, useRef } from 'react';
import {
  sendTransactions,
  useGetAccountInfo,
  transactionServices,
  useGetPendingTransactions
} from '@elrondnetwork/dapp-core';
import {
  TokenPayment,
  MultiESDTNFTTransferPayloadBuilder,
  Address,
  Transaction
} from '@elrondnetwork/erdjs/out';
import NftItem from './NftItem';

export default function SendNft() {
  const apiProvider = 'https://devnet-api.elrond.com';
  const { address, account } = useGetAccountInfo();
  const [userNfts, setUserNfts] = useState(null);
  const [selectedNfts, setSelectedNfts] = useState([]);
  const [refresh, setRefresh] = useState(0);
  const [txId, setTxID] = useState(null);
  const addressDestination = useRef();
  const { pendingTransactionsArray, hasPendingTransactions } =
    useGetPendingTransactions();

  const transactionStatus = transactionServices.useTrackTransactionStatus({
    transactionId: txId,
    onSuccess: () => {
      setSelectedNfts([]);
      setRefresh(refresh + 1);
    },
    onFail: () => {
      console.log('fail');
    },
    onCancelled: () => {
      console.log('cancel');
    }
  });

  useEffect(() => {
    if (hasPendingTransactions) {
      setTxID(pendingTransactionsArray[0][0]);
    }
  }, [hasPendingTransactions]);

  useEffect(() => {
    async function fetchData() {
      const userNftsApi =
        apiProvider + '/accounts/' + address + '/nfts?size=10000';

      fetch(userNftsApi)
        .then((response) => {
          const userNftJson = response.json();
          return userNftJson;
        })
        .then((userNftJson) => {
          let userNftReorg = [];
          userNftJson.forEach((nft) => {
            if (typeof userNftReorg[nft.collection] === 'undefined') {
              userNftReorg[nft.collection] = [];
            }
            userNftReorg[nft.collection].push({
              name: nft.name,
              image: nft.url,
              nonce: nft.nonce,
              identifier: nft.identifier,
              collection: nft.collection
            });
          });
          setUserNfts(userNftReorg);
        });
    }

    fetchData();
  }, [refresh]);

  const sendNft = async () => {
    if (addressDestination.current.value === '') {
      addressDestination.current.focus();
      return;
    }
    const transactions = [];
    const payments = [];
    selectedNfts.forEach((nft) => {
      payments.push(TokenPayment.nonFungible(nft.collection, nft.nonce));
    });
    const payload = new MultiESDTNFTTransferPayloadBuilder()
      .setPayments(payments)
      .setDestination(
        new Address(addressDestination.current.value.toLowerCase())
      )
      .build();

    transactions.push(
      new Transaction({
        nonce: 7,
        receiver: new Address(address),
        data: payload,
        gasLimit: 50000 + 1500 * payload.length() + 1000000 * payments.length,
        chainID: 'D'
      })
    );

    const { sessionId, error } = await sendTransactions({
      transactions: transactions
    });

    setTxID(sessionId);
  };

  const addNft = (item) => {
    const newArr = [
      ...selectedNfts,
      {
        name: item.name,
        nonce: item.nonce,
        collection: item.collection
      }
    ];
    setSelectedNfts(newArr);
  };

  const removeNft = (item) => {
    const newArr = selectedNfts.filter((nft) => nft.nonce !== item.nonce);
    setSelectedNfts(newArr);
  };

  return (
    <div className='p-5'>
      <h1 className='pb-2 text-center'>SELECT NFTS TO TRANSFER</h1>
      <div className='d-flex flex-column flex-wrap '>
        {userNfts &&
          Object.keys(userNfts).map((collection, key) => {
            return (
              <div key={key}>
                <h2>{collection}</h2>
                <div className='d-flex flex-row flex-wrap'>
                  {userNfts[collection].map((nftItem, key2) => {
                    return (
                      <NftItem
                        key={key2}
                        nftItem={nftItem}
                        addNft={addNft}
                        removeNft={removeNft}
                      />
                    );
                  })}
                </div>
              </div>
            );
          })}
      </div>
      <div
        className='sendDiv'
        style={{ display: selectedNfts.length ? 'flex' : 'none' }}
      >
        <input
          ref={addressDestination}
          className='form-control'
          style={{
            width: '300px',
            display: 'inline-block',
            marginRight: '10px'
          }}
          type='text'
          placeholder='Address'
        />
        <button
          className='btn btn-primary'
          onClick={() => {
            sendNft();
          }}
        >
          SEND {selectedNfts.length} NFT{selectedNfts.length > 1 ? 'S' : ''}
        </button>
      </div>
    </div>
  );
}
