import React, { useState, useEffect } from 'react';
import CheckPng from './verifier.png';
import './SendNft.css';

export default function NftItem(props) {
  const [isSelected, setIsSelected] = useState(false);
  const selectThis = (state = null) => {
    if (typeof state === 'boolean') {
      setIsSelected(state);
    } else {
      setIsSelected(!isSelected);
    }

    if (isSelected) {
      props.removeNft(props.nftItem);
    } else {
      props.addNft(props.nftItem);
    }
  };

  useEffect(() => {
    if (props.refreshType === 'unselectAll') {
      selectThis(false);
    }
  }, [props.refreshType]);

  return (
    <div
      onClick={selectThis}
      className='card mr-4 mb-3'
      style={{
        width: ' 18rem',
        backgroundColor: isSelected ? '#28a745' : 'white',
        border: 0,
        cursor: 'pointer'
      }}
    >
      <img
        className='card-img-top p-3'
        src={props.nftItem.image}
        style={{ borderRadius: '1.25rem' }}
      />
      <img
        className='nftChecked'
        src={CheckPng}
        alt='Checked'
        style={{
          display: isSelected ? 'block' : 'none',
          filter: 'hue-rotate(13deg)'
        }}
      />
      <div className='card-body'>
        <h5 style={{ color: isSelected ? 'white' : '#212529' }}>
          {props.nftItem.name}
        </h5>
      </div>
    </div>
  );
}
